
export default function showError(error) {
    if(!error) return 
    switch (error.code) {
        case 199:
            return '';
        case 202:
            return '';
        case 203:
            return '';
        case 210:
            return '';
        case 211:
            return '';
        default:
            return error.error;
    }
}




