import React from 'react'
export default function SignInForm(props) {
    return (
        <form className="signForm" onSubmit={props.onSignIn}>
            <div>
                <input type="text" id="username" name="username"
                    required placeholder=" " 
                    pattern="(?=.*\w).{6,}" 
                    value={props.formData.username}
                    onChange={props.onChangeFormInput.bind(null,'username')}/>
                <label htmlFor="username"></label>
                <div className="requirements">
                    6
                </div>
            </div>


            <div>
                <input type="password" id="password" name="password"
                    required placeholder=" "
                    pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}" 
                    value={props.formData.password}
                    onChange={props.onChangeFormInput.bind(null,'password')}/>
                <label htmlFor="password"></label>
                <div className="requirements">
                    6,,,
                </div>
            </div>

            <input type="submit" value="  " />
            <div>
               <input type="radio" id="forgotpassword" name="forgotpassword" 
                value="forgotpassword" 
                onChange={props.onSwitch}/>
               <label htmlFor="forgotpassword">?</label>
            </div>
            
        </form>
    )
}


